export default {
  name: "charelease",
  data() {
    return {
      cha_datas: undefined,
    }
  },
  created() {
    this.load_cha_release();
  },
  methods: {
    load_cha_release: function (response) {
      this.$axios.get("/app/cha/release", {
        params: {
          start: 0,
          limit: 30
        }
      }).then((response) => {
              this.cha_datas = response.data.datas;

      }).catch((error) => {

      });
    }
  }
}
